.signUpBox {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 100%;
}
.linkBox {
  width: 50%;
  height: 10rem;
  display: flex;
  justify-content: center;
}
.linkBox:hover {
  cursor: pointer;
  background-color: lightgray;
}
.alreadyUserLink:hover {
  border-bottom: 1px solid gray;
} 
