$maincolor: #9acd64;

@import url("https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap");

.header {
  display: flex;
  flex-direction: column;
}
.header_titles {
  width: 70%;
  margin: 0 auto;
  height: 10%;
  display: flex;
  justify-content: flex-start;
  padding: 0.5rem;
  align-items: center;

  .header_titles_logo {
    font-family: "Do Hyeon", sans-serif;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    .header_titles_img {
      padding-bottom: 5px;
      color: $maincolor;
    }
    .header_titles_name {
      letter-spacing: 1.7px;
      padding-left: 10px;
      font-size: 1.5rem;
      color: green;
      opacity: 80%;
      font-weight: bold;
      cursor: pointer;
    }
  }
  .header_titles_ghost {
    flex: 2;
  }
  .header_titles_loginOptions {
    flex: 1;
    position: relative;
    .header_titles_loginOptions_links {
      text-decoration: none;
      margin: 0 3px;
      font-size: 0.9rem;
      color: gray;
      .signUp {
        cursor: pointer;
      }
      .logout {
        cursor: pointer;
        color: black;
      }
    }
  }
}
#headerGhost {
  width: 100vw;
  height: 0.1px;
  border-top: 1px solid gray;
  opacity: 0.3;
}
.header_nav {
  width: 70%;
  margin: 0 auto;
  height: 0.7em;
  display: flex;
  justify-content: flex-start;
  padding: 30px 10px;
  align-items: center;

  span {
    text-decoration: none;
    font-size: 0.9rem;
    font-weight: bold;
    color: black;
    opacity: 0.7;
    margin: 0 1rem;
  }
}
