@import url("https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap");
.home_wrap {
  width: 70%;
  margin: auto;
  display: flex;
  justify-content: space-around;
  height: 100%;
  flex: 1;
}
.home_wrap .home_title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 3rem;
}
.home_wrap .home_title .home_signUp_wrapper {
  display: flex;
}
.home_wrap .home_title .home_signUp_wrapper .home_signUp {
  background-color: #9acd64;
  min-width: 50%;
  height: 2.5rem;
  font-size: 1rem;
  border-radius: 22px;
  margin: 0 10px;
  color: yellow;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 3px 3px 3px gray;
  word-break: break-all;
}
.home_wrap .home_title .home_signUp_wrapper .home_signUp:hover {
  color: black;
}
.home_wrap .home_img {
  margin-top: 5px;
  width: 25rem;
  height: 37rem;
  background-size: cover;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
}/*# sourceMappingURL=Home.css.map */