.signUpStudents_wrap {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  width: 100%;
  background-color: rgba(0, 0, 1, 0.1);
}

.signUp_box {
  padding: 20px;
  width: 50%;
  background-color: white;
  height: auto;
  border: 1px solid lightgray;
  .signUp_box_title {
    width: 100%;
    // flex-direction: ;
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
    border-color: red;
  }
}
