.userCards {
  display: flex;
  flex-direction: column;
  background-color: white;
  min-width: 650px;
  height: auto;
  padding: 6px 12px;
  border-radius: 25px;
  max-height: 400px;
  margin: 10px 0;
  header {
    display: flex;
    justify-content: flex-start;
    height: 40px;
    padding-bottom: 4px;
    .userCards_univ_img {
      width: 6%;
      background-image: url("https://w.namu.la/s/0f1e0805a7a95640ebff206cf2d1cfa3abd8ca876dc7884c57aaad1e31fb0ebbf17d35ff2fd9637544df4232c9fbd357462ef16b56114aaf51fb8305c7c59030907c18a7cef3f4ffd76b739b309e5ff6ef5982ee342d0399f52180a1d28e6878");
      background-size: cover;

      border-radius: 10px;
    }
    .userCards_univ_txt {
      color: rgba(0, 0, 1, 0.8);
      font-size: 15px;
      margin: auto 0;
      margin-left: 5px;
      vertical-align: middle;
    }
  }
  main {
    font-size: 16px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 1px solid lightgray;
    border-top: 1px solid lightgray;
    .userCards_pic_wrap {
      background-color: rgba(60, 200, 80, 0.2);
      border-radius: 60px;
    }
    .userCards_pic {
      margin: 2px;
      width: 100px;
      height: 100px;
      background-image: url("https://img.danawa.com/prod_img/500000/017/350/img/13350017_1.jpg?shrink=330:330&_v=20210224095944");
      background-size: cover;
      border-radius: 50px;
    }
    .userCards_profile {
      display: flex;
      flex-direction: column;
      padding-top: 10px;
      padding-left: 10px;
    }
  }
}
